/**********************************************************************
                              ENGINE ISO VOXEL v2.0
***********************************************************************

 Y, X, Z:
    Z
    |
   / \
  X   Y

iso-box :
    1 - tY = taille en Y
    2 - tX = taille en X
    3 - tZ = taille en Z
    4 - pY = placement en Y
    5 - pX = placement en X
    6 - pZ = placement en Z
    7 - c = couleur en rvb
    8 - sd = activer ombre
    9 - go = lancement de l'animation

    name:(tY,tX,tZ,pY,pX,pZ,c,sd,go) 
*/
/* html{
  cursor: crosshair;
} */
/* Point origine XYZ */
$duration: 4s;
.container-iso {
  width:0px;
  height:0px;
  transform-style: preserve-3d;
  transform: scale(6);

  animation: scale-down $duration 1 both;
  @keyframes scale-down {
    to { 
      transform: scale(0.8);
    }
  }
}

/* Dgrad */
@mixin linear-gradient($direction, $color-stops...) {
  background: nth(nth($color-stops, 1), 1);
  background: -webkit-linear-gradient(legacy-direction($direction), $color-stops);
  background: linear-gradient($direction, $color-stops);
}

