  cad_1: (1px, 40px, 20px, -120px, 130px, 090px, rgba(135,206,235,1), false, $tMeuble+.75),
  cad_2: (1px, 5px, 5px, -90px, 130px, 97px, rgba(255,255,0,1), false, $tMeuble+1),
  cad_3: (5px, 5px, 35px, -80px, 130px, 80px, $bois, false, $tMeuble),
  cad_4: (5px, 40px, 5px, -120px, 130px, 080px, $bois, false, $tMeuble),
  cad_5: (5px, 40px, 5px, -120px, 130px, 110px, $bois, false, $tMeuble+.5),
  cad_6: (5px, 5px, 35px, -125px, 130px, 80px, $bois, false, $tMeuble),  
);


/***********************************
            CONSTRUCTEUR
************************************/
$vitesse: 1;
$timing-function: cubic-bezier(.68,-.55,.265,1.55);

@each $name, $iso-box in $iso-boxs {
  ##{$name} { /* top */
    position:absolute;
    animation: #{$name} $vitesse+s (nth($iso-box,9)+$vitesse)+s 1 $timing-function forwards;
    opacity: 0;
    width: nth($iso-box,1);
    height: nth($iso-box,2);
    transform:  rotate(210deg) skew(-30deg) scaleY(0.86602)
                translateY( (- nth($iso-box,1)/3.6) + nth($iso-box,2) + nth($iso-box,4) + (nth($iso-box,3) + nth($iso-box,6)) )
                translateX( (- nth($iso-box,1)/4.6) + nth($iso-box,2)/2 + nth($iso-box,5) + (nth($iso-box,3) + nth($iso-box,6)) );

    @if nth($iso-box,8){
      @include linear-gradient(-135deg,darken(nth($iso-box,7), 0%) 0%, darken(nth($iso-box,7), 10%) 100%);
    }@else{
      background-color: nth($iso-box,7);
    }

    transition: all 0.6s linear;
  
    &::before{ /* left */
      content:"";
      animation: #{$name}-before $vitesse+s (nth($iso-box,9)+$vitesse)+s 1 $timing-function forwards;
      display: block;
      position:absolute;
      width: nth($iso-box,1);
      height:nth($iso-box,3);
    
      transform:  rotate(0deg) skewX(45deg) scaleY(1)
                  translate((nth($iso-box,3)/2)-0.75,(- nth($iso-box,3))+0.75);
                    
      @if nth($iso-box,8){
        @include linear-gradient(-45deg,darken(nth($iso-box,7), 0%) 0%, darken(nth($iso-box,7), 10%) 100%);
      }@else{
        background-color: darken( nth($iso-box,7) , 5% );
      }
    }
    
    &::after{ /* right */
      content:"";
      animation: #{$name}-after $vitesse+s (nth($iso-box,9)+$vitesse)+s 1 $timing-function forwards;
      display: block;
      position:absolute;
      width: nth($iso-box,2);
      height:nth($iso-box,3);
      transform:  rotate(90deg) skew(-45deg) scaleY(1)
                  translate((nth($iso-box,2)-(nth($iso-box,3)/2))-0.75,(nth($iso-box,3)/2+(nth($iso-box,2)/2))-0.75);
      
      @if nth($iso-box,8){
        @include linear-gradient(100deg,darken(nth($iso-box,7), 10%) 0%, darken(nth($iso-box,7), 20%) 100%);

      }@else{
        background-color: darken( nth($iso-box,7) , 10% );
      }
    }
  }
}

/* anim construct pousse */
@each $name, $iso-box in $iso-boxs {
  @keyframes #{$name} {
    0% {
      opacity:0;
      margin-top: nth($iso-box,3);
    }
    25% {
      opacity:1;
    }
    100% {
      opacity:1;
      margin:0;
    }
  }
  @keyframes #{$name}-before {     
    0% {
      height: 0;
      margin-top: nth($iso-box,3);
      margin-left: nth($iso-box,3)/2;
    }
    100% {
      height: nth($iso-box,3);
      margin:0;
    }
  }
  @keyframes #{$name}-after {
    0% {
      height: 0px;
      margin-top: nth($iso-box,3);
      margin-left: nth($iso-box,3)/2;
    }
    100% {
      height: nth($iso-box,3);
      margin:0;
    }
  }
}
